import './About.css';
import { ProfileInfo, SkillsSection } from '../../components/index'
import { useEffect, useState } from 'react';

const About = () => {
    const [aboutUs, setAboutUs] = useState([]);
    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('https://osamabraidy.000webhostapp.com/dashboard/API/aboutAPI.php', {
                    method: 'GET',
                    mode: 'cors',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                });
                const result = await response.json();
                setAboutUs(result[0].Aboutus);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };
        fetchData();
    }, []);

    return (
        <section id="about" className="about-mf route">
            <div className="container">
                <div className="row">
                    <div className="col-sm-12">
                        <div className="box-shadow-full">
                            <div className="row">
                                <div className="col-md-6">
                                    <ProfileInfo />
                                    <SkillsSection />
                                </div>
                                <div className="col-md-6">
                                    <div className="about-me pt-4 pt-md-0">
                                        <div className="title-box-2">
                                            <h5 className="title-left">About me</h5>
                                        </div>
                                        <p className="lead">
                                            {aboutUs}
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default About;
