import './ProfileInfo.css'
import AboutInfo from '../AboutInfo/AboutInfo';
import osamaAlBraidyImage from '../../assets/OsamaAlBraidy.jpg'
import { useEffect, useState } from 'react';

const ProfileInfo = () => {
    const [data, setData] = useState([]);
    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('https://osamabraidy.000webhostapp.com/dashboard/API/aboutAPI.php', {
                    method: 'GET',
                    mode: 'cors',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                });
                const result = await response.json();
                setData(result[0]);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };
        fetchData();
    }, []);
    return (
        <div className="row">
            <div className="col-sm-6 col-md-5">
                <div className="about-img">
                    <img src={osamaAlBraidyImage} alt='' width={150} height={150} className='img-fluid rounded b-shadow-a' />
                </div>
            </div>
            <div className="col-sm-6 col-md-7">
                <div className="about-info">
                    <AboutInfo
                        title="Name: "
                        details={data.Name}
                    />
                    <AboutInfo
                        title="Profile: "
                        details={data.Profile}
                    />
                    <AboutInfo
                        title="Email: "
                        details={data.Email}
                    />
                    <AboutInfo
                        title="Phone: "
                        details={data.Phone}
                    />
                </div>
            </div>
        </div>
    );
};

export default ProfileInfo;

