import React, { useEffect, useState } from 'react';
import CountUp from 'react-countup';
import './CounterSection.css';
import check from '../../assets/Counter/check-lg.svg';
import journal from '../../assets/Counter/journal-richtext.svg';

const CounterSection = () => {
    const [countersInView, setCountersInView] = useState(false);
    const [data, setData] = useState([]);

    // eslint-disable-next-line react-hooks/exhaustive-deps
    const handleScroll = () => {
        const scrollPosition = window.scrollY + window.innerHeight;
        const elementPosition = document.querySelector('.section-counter').offsetTop;

        if (scrollPosition > elementPosition && !countersInView) {
            setCountersInView(true);
        }
    };

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, [countersInView, handleScroll]);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('https://osamabraidy.000webhostapp.com/dashboard/API/numberofprojectAPI.php', {
                    method: 'GET',
                    mode: 'cors',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                });
                const result = await response.json();
                setData(result[0]);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, [])

    return (
        <div className="section-counter paralax-mf bg-image">
            <div className="container position-relative">
                <div className="row">
                    <div className="col-sm-6 col-lg-6">
                        <div className="counter-box counter-box pt-4 pt-md-0">
                            <div className="counter-ico">
                                <div className="ico-circle">
                                    <img src={check} alt="" />
                                </div>
                            </div>
                            <div className="counter-num">
                                <CountUp start={0} end={countersInView ? data.ProjectsNum : 0} duration={2} className="counter purecounter" />
                                <p className="counter-text">WORKS COMPLETED</p>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm-6 col-lg-6">
                        <div className="counter-box pt-4 pt-md-0">
                            <div className="counter-ico">
                                <div className="ico-circle">
                                    <img src={journal} alt="" />
                                </div>
                            </div>
                            <div className="counter-num">
                                <CountUp start={0} end={countersInView ? data.Experience : 0} duration={2} className="counter purecounter" />
                                <p className="counter-text">YEARS OF EXPERIENCE</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default CounterSection;
